@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);


.main-login{
    width:50%;
    text-align: center;
    margin: 0 auto;
}

.main-login-cont{
font-size: inherit;
}

.login-btn{
    width: 200px;
    height: 70px;
    font-size: 1.3rem;
    background: rgb(9, 23, 53);
    color: #fff;
}


h1{
    font-size: 100px;
}
h2{
    font-size: 65px;
}


.menu{
    height:100vh;
    background: hsla(205, 46%, 10%, 1);
    min-width: 140px;
    max-width: 200px;
    border-right: 1px solid #fff;
    text-align: center;
    position:sticky;
}

.menuBtn{
    width: 100%;
    height: 40px;
    color: #fff;
    background: rgb(41, 80, 107);
    font-size: 20px;
    border: none;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
}

.stat{
    font-size: 35px;
}

.stats-icon{
    float:left;
    margin-top: 5px;
}



.dashboard{
    display: flex;
}

.collection-container{
    display: grid;
  grid-template-columns: repeat(5, 220px);
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  flex: 2 1;
  height: 600px;
  text-align: center;
  width: 100%;
  
}

.cont-cont{
    display: flex;
    width: 100%;
    margin: 0 auto;
    margin-top: 140px;
    justify-content: space-around;
}

.right{
    flex:1 1;
    margin-top: 270px;
    min-width: 30px;
}

.page-num{
    width: 100%;
    text-align: center;
    font-size: 22px;
}

.big-cont{
    width: 100%;
    text-align: center;
}

.user-info{
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 120px;
    background: hsla(205, 46%, 10%, 1);
    border-bottom: 1px solid #fff;
    font-size: 2.3rem;
}

.user{
    margin-top: 35px;
}


.mine-wrap{
    border: 2px solid yellow;
    width: 100%;
    font-size: 35px;
}

.minedd{
    padding-left: 55px;
}
.card{
    min-width: 200px;
    max-width: 200px;
    min-height: 280px;
    max-height:280px ;
    border-radius: 10px;
    border: 1px solid;
    margin:0 auto;
  
}

.options-card{
    min-width: 200px;
    max-width: 200px;
    min-height: 280px;
    max-height:280px ;
    border-radius: 10px;
    border: 1px solid;
    margin:0 auto;
  
}

.common{
    border: 3px solid rgb(51, 51, 51);
    background:rgb(155, 155, 155);
    color: rgb(65, 65, 65);
}

.rare{
    border: 5px solid rgb(117, 202, 117);
    background:rgb(44, 109, 44);
    color: rgb(0, 223, 0)
}


.epic{
    border: 7px solid rgb(51, 18, 51);
    background:rgb(75, 37, 75);
    box-shadow: 0px 0px 40px 10px rgba(204, 0, 204, 0.664);
    color: rgb(231, 0, 231);
}

.legendary{
    border: 7px solid rgb(255, 166, 0);
    background:rgb(151, 73, 1);
    box-shadow: 0px 0px 40px 20px rgb(255, 115, 0);
    color: orange;
}

.card-img-cont{
    width: 100%;
    height: 200px;
    border-bottom:2px solid #fff ;
    border-radius: 5px;

}




.card-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
}

.stats{
    font-size: 1.2rem;
}
.card-stats{
  text-align: center;
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

.card-side{
    height: 94%;
    width: 10%;
    left: 20%;
    background-color: white;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    
}

.delete-card{
    width: 100%;
    cursor: pointer;
}

.add-card-deck{
    width: 100%;
}

.delete-card{
    font-size: 30px;
    border: 1px solid;
}



.empty-card{
    min-width: 200px;
    max-width: 200px;
    min-height: 280px;
    max-height:280px ;
    border-radius: 10px;
    border: 1px solid grey;
    margin:0 auto;
    text-align: center;
    background: rgb(62, 62, 68);
}

.empty-text{
    margin-top:125px;
    font-size: 20px;
}
.square{
    width: 120px;
    height: 120px;
    background: green;
    margin: 2px;
    border: 1px solid #fff;
    font-size: 110px;
}

.alter-square{
    width: 120px;
    height: 120px;
    background: yellow;
    margin: 2px;
    border: 1px solid #fff;
    font-size: 110px;
}

.square-cont{
    margin: 0 auto;
    text-align: center;
    margin-top: 60px;
}

.squares{
    display: flex;
    justify-content:center;
    width: 100%;
    
}

.gamble{
    display: flex;
}

.game-cont{
    width: 100%;
}

.button-cont{
    width: 100%;
    text-align: center;
}

.game-button{
    width: 50px;
    height: 30px;
    font-size: 20px;
    margin: 10px;

}

.bet-btn-cont{
    margin: 0 auto;
    text-align: center;
    margin-top: 40px;
}

.bet-btn{
    width: 100px;
    height: 50px;
    font-size: 20px;
    margin: 10px;
}

input{
    height: 50px;
    width: 150px;
    font-size: 30px;
    padding-bottom: 7px;
}

.sess-results{
    font-size: 40px;
    margin:0 auto;
    text-align: center;
    padding-top: 4px;

}

.positive{
    font-size: 40px;
    color: green;
}

.negative{
    font-size: 40px;
    color: rgb(243, 82, 82);
}

.result-cont{
    display: flex;
    justify-content: center;
    width: 380px;
    margin: 0 auto;
    margin-top: 200px;
}

.coinn{
    padding-top: 9px;
}


.result-wrap{
    border: 1px solid #fff;
    background: rgb(41, 41, 41);
    padding-left:6px ;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;


}


body{
    background: rgb(26, 51, 70);

    

    height: 100vh;

    font-size: 1rem;
    color: #fff;

    font-family: Bebas Neue,Arial, Helvetica, sans-serif;

}
